body {
  margin: 0;
}

.about-container {
  width: 100%;
  background: #605B56;
  height: 490px;
}

.topnav {
  text-align: right;
  background: #8EB8E5;
  width: 100%;
  overflow: hidden;
}
.topnav a {
  font-size: 20px;
  font-weight: 300;
  padding: 14px 16px;
  text-decoration: none;
  color: #F1FFFA;
  display: inline-block;
  font-family: Helvetica;
  -webkit-transition: -webkit-transform .3s ease-out;
  transition: -webkit-transform .3s ease-out;
  -o-transition: transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.topnav a:hover {
  color: black;
  -webkit-transform: translate(0px, 3px);
      -ms-transform: translate(0px, 3px);
          transform: translate(0px, 3px);
}
.topnav .icon {
  display: none;
  font-size: 15px;
}

@media screen and (max-width: 600px) {
  .topnav a {
    display: none;
  }

  .topnav .icon {
    display: block;
    float: right;
  }

  .topnav.responsive {
    position: relative;
  }

  .topnav.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }

  .topnav.responsive a {
    float: none;
    display: block;
    text-align: center;
  }
}



